import { useState } from "react";
import { Link } from "react-router-dom";
import { motion, AnimatePresence } from "framer-motion";
import { Button } from "@/components/ui/button";
import { Sheet, SheetContent, SheetTrigger } from "@/components/ui/sheet";
import { Badge } from "@/components/ui/badge";
import {
  ShoppingCart,
  Menu,
  Search,
  User,
  X,
  Heart,
  Package,
  ChevronRight,
} from "lucide-react";
import { cn } from "@/lib/utils";
import { 
  NavigationMenu,
  NavigationMenuContent,
  NavigationMenuItem,
  NavigationMenuLink,
  NavigationMenuList,
  NavigationMenuTrigger,
} from "@/components/ui/navigation-menu";
import { Dialog, DialogContent } from "@/components/ui/dialog";

const categories = [
  {
    name: "Clothing",
    subcategories: ["T-Shirts", "Jeans", "Dresses", "Jackets"],
    featured: [
      { name: "New Arrivals", href: "#new-arrivals" },
      { name: "Summer Collection", href: "#summer-collection" },
    ],
  },
  {
    name: "Electronics",
    subcategories: ["Smartphones", "Laptops", "Audio", "Accessories"],
    featured: [
      { name: "Latest Tech", href: "#latest-tech" },
      { name: "Best Sellers", href: "#best-sellers" },
    ],
  },
  {
    name: "Home & Living",
    subcategories: ["Furniture", "Decor", "Kitchen", "Bath"],
    featured: [
      { name: "Interior Inspiration", href: "#interior" },
      { name: "Sale Items", href: "#sale" },
    ],
  },
];

// Mock cart items for the prototype
const initialCartItems = [
  {
    id: 1,
    name: "Minimalist Watch",
    price: 149.99,
    image: "https://images.unsplash.com/photo-1523275335684-37898b6baf30",
    quantity: 1,
  },
  {
    id: 2,
    name: "Wireless Earbuds",
    price: 89.99,
    image: "https://images.unsplash.com/photo-1572569511254-d8f925fe2cbb",
    quantity: 1,
  },
];

const Header = () => {
  const [isOpen, setIsOpen] = useState(false);
  const [cartOpen, setCartOpen] = useState(false);
  const [searchOpen, setSearchOpen] = useState(false);
  const [cartItems, setCartItems] = useState(initialCartItems);
  
  const cartItemsCount = cartItems.reduce((total, item) => total + item.quantity, 0);
  const cartTotal = cartItems.reduce((total, item) => total + (item.price * item.quantity), 0);
  
  const removeFromCart = (id: number) => {
    setCartItems(prev => prev.filter(item => item.id !== id));
  };
  
  const updateQuantity = (id: number, newQuantity: number) => {
    if (newQuantity < 1) return;
    setCartItems(prev => 
      prev.map(item => 
        item.id === id ? { ...item, quantity: newQuantity } : item
      )
    );
  };

  return (
    <header className="sticky top-0 z-50 w-full bg-background/95 backdrop-blur supports-[backdrop-filter]:bg-background/60 border-b border-border">
      <div className="container flex h-16 items-center">
        <div className="mr-4 hidden md:flex">
          <Link to="/" className="flex items-center space-x-2">
            <span className="font-bold text-xl bg-gradient-to-r from-primary to-primary/80 bg-clip-text text-transparent py-1">ShopSmart</span>
          </Link>
          <NavigationMenu className="ml-6">
            <NavigationMenuList>
              {categories.map((category) => (
                <NavigationMenuItem key={category.name}>
                  <NavigationMenuTrigger className="text-foreground/70 hover:text-foreground">
                    {category.name}
                  </NavigationMenuTrigger>
                  <NavigationMenuContent>
                    <div className="w-[400px] p-4 md:w-[500px] lg:w-[600px]">
                      <div className="grid gap-3 md:grid-cols-2">
                        <div>
                          <h4 className="mb-3 text-sm font-medium leading-none text-primary">
                            Categories
                          </h4>
                          <div className="grid gap-3 text-sm">
                            {category.subcategories.map((subcat) => (
                              <Link
                                key={subcat}
                                to={`/category/${category.name.toLowerCase()}/${subcat.toLowerCase().replace(/ /g, '-')}`}
                                className="group flex items-center gap-2 rounded-md hover:bg-accent px-2 py-1.5"
                              >
                                {subcat}
                                <ChevronRight className="ml-auto h-4 w-4 opacity-0 group-hover:opacity-100" />
                              </Link>
                            ))}
                          </div>
                        </div>
                        <div>
                          <h4 className="mb-3 text-sm font-medium leading-none text-primary">
                            Featured
                          </h4>
                          <div className="grid gap-3">
                            {category.featured.map((item) => (
                              <Link
                                key={item.name}
                                to={item.href.startsWith('#') ? `/coming-soon?feature=${item.name.toLowerCase().replace(/ /g, '-')}` : item.href}
                                className="group flex h-16 items-center justify-between space-x-4 rounded-md border p-2 hover:bg-accent"
                              >
                                <div className="font-medium">{item.name}</div>
                                <ChevronRight className="h-5 w-5 opacity-0 group-hover:opacity-100" />
                              </Link>
                            ))}
                          </div>
                        </div>
                      </div>
                    </div>
                  </NavigationMenuContent>
                </NavigationMenuItem>
              ))}
              <NavigationMenuItem>
                <Link to="/deals" className="text-foreground/70 hover:text-foreground flex h-10 w-max items-center justify-center rounded-md px-4 py-2 text-sm font-medium">
                  Deals
                </Link>
              </NavigationMenuItem>
              <NavigationMenuItem>
                <Link to="/new-arrivals" className="text-foreground/70 hover:text-foreground flex h-10 w-max items-center justify-center rounded-md px-4 py-2 text-sm font-medium">
                  New Arrivals
                </Link>
              </NavigationMenuItem>
            </NavigationMenuList>
          </NavigationMenu>
        </div>

        <Sheet open={isOpen} onOpenChange={setIsOpen}>
          <SheetTrigger asChild className="md:hidden">
            <Button variant="ghost" size="icon" className="mr-2">
              <Menu className="h-5 w-5" />
              <span className="sr-only">Toggle menu</span>
            </Button>
          </SheetTrigger>
          <SheetContent side="left" className="w-[300px]">
            <Link to="/" className="flex items-center mb-6" onClick={() => setIsOpen(false)}>
              <span className="font-bold text-xl bg-gradient-to-r from-primary to-primary/80 bg-clip-text text-transparent py-1">ShopSmart</span>
            </Link>
            <nav className="space-y-6">
              {categories.map((category) => (
                <div key={category.name} className="space-y-3">
                  <h4 className="font-medium text-primary">{category.name}</h4>
                  <div className="ml-4 space-y-2">
                    {category.subcategories.map((subcat) => (
                      <Link 
                        key={subcat}
                        to={`/category/${category.name.toLowerCase()}/${subcat.toLowerCase().replace(/ /g, '-')}`} 
                        className="block text-foreground/70 hover:text-foreground"
                        onClick={() => setIsOpen(false)}
                      >
                        {subcat}
                      </Link>
                    ))}
                  </div>
                </div>
              ))}
              <div className="space-y-3">
                <Link 
                  to="/deals" 
                  className="block text-foreground font-medium hover:text-primary"
                  onClick={() => setIsOpen(false)}
                >
                  Deals
                </Link>
                <Link 
                  to="/new-arrivals" 
                  className="block text-foreground font-medium hover:text-primary"
                  onClick={() => setIsOpen(false)}
                >
                  New Arrivals
                </Link>
              </div>
              <div className="pt-4 border-t border-border">
                <Button className="w-full mb-2" onClick={() => { setIsOpen(false); window.location.href = '/login'; }}>
                  <User className="mr-2 h-4 w-4" /> Sign In
                </Button>
              </div>
            </nav>
          </SheetContent>
        </Sheet>

        <Link to="/" className="mr-auto md:hidden">
          <span className="font-bold text-xl bg-gradient-to-r from-primary to-primary/80 bg-clip-text text-transparent py-1">ShopSmart</span>
        </Link>

        <div className="flex items-center gap-2">
          <Button 
            variant="ghost" 
            size="icon" 
            className="relative"
            onClick={() => setSearchOpen(true)}
          >
            <Search className="h-5 w-5" />
            <span className="sr-only">Search</span>
          </Button>
          
          <Button 
            variant="ghost" 
            size="icon"
            asChild
          >
            <Link to="/account">
              <User className="h-5 w-5" />
              <span className="sr-only">Account</span>
            </Link>
          </Button>
          
          <Button 
            variant="ghost" 
            size="icon"
            className="relative"
            onClick={() => setCartOpen(true)}
          >
            <ShoppingCart className="h-5 w-5" />
            {cartItemsCount > 0 && (
              <Badge className="absolute -top-1 -right-1 h-5 w-5 flex items-center justify-center rounded-full p-0 text-xs">
                {cartItemsCount}
              </Badge>
            )}
            <span className="sr-only">Cart</span>
          </Button>
        </div>
      </div>

      <Dialog open={cartOpen} onOpenChange={setCartOpen}>
        <DialogContent className="sm:max-w-[425px]">
          <div className="flex items-center justify-between mb-4">
            <h2 className="text-xl font-semibold">Your Cart</h2>
            <Button variant="ghost" size="icon" onClick={() => setCartOpen(false)}>
              <X className="h-4 w-4" />
            </Button>
          </div>
          
          {cartItems.length === 0 ? (
            <div className="text-center py-8">
              <div className="mx-auto w-16 h-16 rounded-full bg-secondary flex items-center justify-center mb-4">
                <ShoppingCart className="h-8 w-8 text-primary" />
              </div>
              <h3 className="text-lg font-medium mb-2">Your cart is empty</h3>
              <p className="text-muted-foreground mb-4">Looks like you haven't added anything to your cart yet.</p>
              <Button onClick={() => setCartOpen(false)}>Continue Shopping</Button>
            </div>
          ) : (
            <>
              <div className="space-y-4 max-h-[300px] overflow-auto pr-2">
                {cartItems.map((item) => (
                  <div key={item.id} className="flex gap-4 pb-4 border-b">
                    <div className="h-20 w-20 rounded-md overflow-hidden bg-secondary flex-shrink-0">
                      <img src={item.image} alt={item.name} className="h-full w-full object-cover" />
                    </div>
                    <div className="flex-1">
                      <div className="flex justify-between">
                        <h4 className="font-medium">{item.name}</h4>
                        <Button 
                          variant="ghost" 
                          size="icon" 
                          className="h-6 w-6" 
                          onClick={() => removeFromCart(item.id)}
                        >
                          <X className="h-3 w-3" />
                        </Button>
                      </div>
                      <div className="text-sm text-muted-foreground mb-2">${item.price.toFixed(2)}</div>
                      <div className="flex items-center">
                        <Button 
                          variant="outline" 
                          size="icon" 
                          className="h-6 w-6 rounded-r-none" 
                          onClick={() => updateQuantity(item.id, item.quantity - 1)}
                        >
                          -
                        </Button>
                        <div className="h-6 px-2 flex items-center justify-center border-y">
                          {item.quantity}
                        </div>
                        <Button 
                          variant="outline" 
                          size="icon" 
                          className="h-6 w-6 rounded-l-none" 
                          onClick={() => updateQuantity(item.id, item.quantity + 1)}
                        >
                          +
                        </Button>
                      </div>
                    </div>
                  </div>
                ))}
              </div>
              <div className="border-t pt-4 mt-4">
                <div className="flex justify-between mb-4">
                  <span className="font-medium">Total</span>
                  <span className="font-bold">${cartTotal.toFixed(2)}</span>
                </div>
                <Button className="w-full" onClick={() => { setCartOpen(false); window.location.href = '/coming-soon?feature=checkout'; }}>Checkout</Button>
                <Button variant="outline" className="w-full mt-2" onClick={() => setCartOpen(false)}>
                  Continue Shopping
                </Button>
              </div>
            </>
          )}
        </DialogContent>
      </Dialog>

      <Dialog open={searchOpen} onOpenChange={setSearchOpen}>
        <DialogContent className="sm:max-w-[600px]">
          <div className="flex items-center justify-between mb-4">
            <h2 className="text-xl font-semibold">Search Products</h2>
            <Button variant="ghost" size="icon" onClick={() => setSearchOpen(false)}>
              <X className="h-4 w-4" />
            </Button>
          </div>
          
          <div className="relative">
            <Search className="absolute left-3 top-1/2 -translate-y-1/2 h-4 w-4 text-muted-foreground" />
            <input
              type="text"
              placeholder="Search for products..."
              className="w-full rounded-md border border-input pl-10 pr-4 py-2 focus:outline-none focus:ring-2 focus:ring-primary"
              autoFocus
            />
          </div>
          
          <div className="mt-4">
            <h3 className="text-sm font-medium text-muted-foreground mb-2">Popular Searches</h3>
            <div className="flex flex-wrap gap-2">
              {["summer dress", "wireless headphones", "smart watch", "running shoes", "kitchen gadgets"].map((term) => (
                <Badge key={term} variant="outline" className="cursor-pointer hover:bg-secondary">
                  {term}
                </Badge>
              ))}
            </div>
          </div>
          
          <div className="mt-4">
            <h3 className="text-sm font-medium text-muted-foreground mb-2">Recent Searches</h3>
            <div className="space-y-2">
              {["laptop sleeve", "water bottle", "sunglasses"].map((term) => (
                <div key={term} className="flex items-center group">
                  <Button variant="ghost" size="icon" className="h-6 w-6 mr-1 opacity-0 group-hover:opacity-100">
                    <X className="h-3 w-3" />
                  </Button>
                  <span>{term}</span>
                </div>
              ))}
            </div>
          </div>
        </DialogContent>
      </Dialog>
    </header>
  );
};

export default Header;