import { Toaster } from "@/components/ui/sonner";
import { TooltipProvider } from "@/components/ui/tooltip";
import { QueryClient, QueryClientProvider } from "@tanstack/react-query";
import { BrowserRouter, Routes, Route } from "react-router-dom";
import Home from "./pages/Home";
import Login from "./pages/Login";
import Signup from "./pages/Signup";
import Contact from "./pages/Contact";
import About from "./pages/About";
import Terms from "./pages/Terms";
import Privacy from "./pages/Privacy";
import ComingSoon from "./pages/ComingSoon";
import ErrorPage from "./pages/ErrorPage";

const queryClient = new QueryClient();

const App = () => (
  <QueryClientProvider client={queryClient}>
    <TooltipProvider>
      <Toaster />
      <BrowserRouter>
        <Routes>
          <Route path="/" element={<Home />} />
          <Route path="/login" element={<Login />} />
          <Route path="/signup" element={<Signup />} />
          <Route path="/contact" element={<Contact />} />
          <Route path="/about" element={<About />} />
          <Route path="/terms" element={<Terms />} />
          <Route path="/privacy" element={<Privacy />} />
          <Route path="/coming-soon" element={<ComingSoon />} />
          <Route path="/category/:categoryName" element={<ComingSoon />} />
          <Route path="/category/:categoryName/:subcategoryName" element={<ComingSoon />} />
          <Route path="/deals" element={<ComingSoon />} />
          <Route path="/new-arrivals" element={<ComingSoon />} />
          <Route path="/payment-methods" element={<ComingSoon />} />
          <Route path="/returns" element={<ComingSoon />} />
          <Route path="/shipping" element={<ComingSoon />} />
          <Route path="/faq" element={<ComingSoon />} />
          <Route path="/forgot-password" element={<ComingSoon />} />
          <Route path="/account" element={<ComingSoon />} />
          <Route path="/sitemap" element={<ComingSoon />} />
          <Route path="*" element={<ErrorPage />} />
        </Routes>
      </BrowserRouter>
    </TooltipProvider>
  </QueryClientProvider>
);

export default App;
