import { Link, useLocation, useParams } from "react-router-dom";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { toast } from "sonner";
import { ArrowLeft, Clock } from "lucide-react";

const ComingSoon = () => {
  const location = useLocation();
  const params = useParams();
  const searchParams = new URLSearchParams(location.search);
  const feature = searchParams.get('feature') || params.categoryName || params.subcategoryName || 'This feature';
  
  // Format the feature name for display
  const formatFeatureName = (name: string) => {
    return name
      .split('-')
      .map(word => word.charAt(0).toUpperCase() + word.slice(1))
      .join(' ');
  };

  const displayFeature = formatFeatureName(feature);
  
  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    toast.success("Thanks for subscribing!", {
      description: `We'll notify you when ${displayFeature} launches.`,
    });
    (e.target as HTMLFormElement).reset();
  };

  return (
    <div className="min-h-screen flex flex-col items-center justify-center p-4 bg-secondary/30">
      <div className="max-w-md w-full text-center">
        <div className="w-16 h-16 bg-primary/10 rounded-full flex items-center justify-center mx-auto mb-6">
          <Clock className="h-8 w-8 text-primary" />
        </div>
        
        <h1 className="text-3xl font-bold mb-2">Coming Soon</h1>
        
        <p className="text-xl font-medium text-primary mb-2">
          {displayFeature}
        </p>
        
        <p className="text-muted-foreground mb-6">
          We're working hard to bring you this feature. Sign up below to be notified when it launches.
        </p>
        
        <form onSubmit={handleSubmit} className="mb-8">
          <div className="flex gap-2">
            <Input 
              type="email" 
              placeholder="Enter your email address" 
              className="flex-1" 
              required 
            />
            <Button type="submit">Notify Me</Button>
          </div>
        </form>
        
        <Button variant="outline" asChild>
          <Link to="/" className="flex items-center gap-2">
            <ArrowLeft className="h-4 w-4" />
            Back to Home
          </Link>
        </Button>
      </div>
    </div>
  );
};

export default ComingSoon;